@use "./common/reset";
@use "./common/global_vars";

@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;0,300;0,400;0,500;0,600;1,200;1,300;1,400;1,500;1,600&display=swap");

html {
  font-family: "Montserrat", sans-serif;
}

body {
  display: grid;
  grid-template-rows: auto 1fr auto;
  height: 100vh;

  background: var(--color-bg);

  background: radial-gradient(
    circle,
    // rgba(2, 0, 36, 1) 0%,
    // rgba(1, 12, 54, 1) 0%,
    rgba(52, 52, 145, 1) 26%,
    rgba(1, 12, 54, 1) 100%
  );

  // background: #353591;
  // background: -webkit-radial-gradient(center, #353591, #020024);
  // background: -moz-radial-gradient(center, #353591, #020024);
  // background: radial-gradient(ellipse at center, #353591, #020024);
}

.wrapper {
  max-width: 1600px;
  margin: 0 auto;
}

canvas {
  position: absolute;
  width: 100%;
  height: 100%;
  display: block;
  // background: var(--color-bg);
}

.icon {
  fill: none;
  stroke: var(--color-text);
  stroke-width: 6px;
}

header {
  width: 100%;
  padding: 2em 2em 0;
  display: flex;
  justify-content: space-between;
  // border-bottom: #fff 2px solid;
  // box-shadow: 8px 10px 10px 1px rgba(0,0,0,0.5);
  // box-shadow: 0 20px 16px -16px rgba(0,0,0,0.6);

  h1 {
    display: inline-block;
    height: auto;
    font-size: 4rem;
    text-transform: uppercase;
    color: var(--color-text);
    line-height: 1.1;
  }

  div {
    display: grid;
    grid-template-rows: auto auto 1fr;
    // row-gap: 4px;
    justify-items: end;
    align-content: center;
    padding: 10px 0;
    // row-gap: 2em;
  }

  a {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    color: var(--color-text);
    font-size: 1.5em;
    text-decoration: none;
  }

  svg {
    height: 1em;
    margin-left: 0.5em;
  }

  button {
    align-self: end;
    width: 100%;
    height: 100%;
    background: var(--color-text);
    border: none;
    cursor: pointer;
    height: 3em;
    padding: 0.5em;
    border-radius: 20px;
    font-size: 1em;
    transition: all .15s;

    &:hover {
      background: #337ab7;
      color: #fff;
      transform: scale(1.05);
    }
  }
}

main {
  position: relative;
  display: flex;
  width: 100%;
  // padding: 2em;

  nav {
    margin: auto;
    padding: 0 2em;
    width: 100%;

    ul {
      display: grid;
      grid-template-columns: repeat(2, 400px);
      row-gap: 5em;

      justify-content: space-between;
      list-style: none;
    }

    li {
      position: relative;
      transition: all 0.3s;
      // min-height: 110px;
      // max-width: 600px;

      // &:hover {
      //   // transform: scale(1.02);
      //   // zoom: 105%;
      // }

      &:hover::before {
        transform: translate(15px, -15px);
      }

      &:hover::after {
        transform: translate(-15px, 15px);
      }

      &::before {
        content: "";
        position: absolute;
        bottom: -15px;
        left: -15px;
        width: 50%;
        height: 100%;
        background: #337ab7;
        clip-path: polygon(
          calc(100% - 50px) 0,
          100% 30%,
          100% 100%,
          40px 100%,
          0 50%,
          0 0
        );
        transition: all 0.3s;
      }

      &::after {
        content: "";
        position: absolute;
        top: -15px;
        right: -15px;
        width: 50%;
        height: 100%;
        background: #337ab7;
        clip-path: polygon(
          calc(100% - 50px) 0,
          100% 30%,
          100% 100%,
          40px 100%,
          0 50%,
          0 0
        );
        transition: all 0.3s;
      }

      a {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        z-index: 1;
        padding: 1em;
        text-decoration: none;
        background: #fff;
        min-height: 94px;
        font-weight: 500;

        color: #000;

        clip-path: polygon(
          calc(100% - 50px) 0,
          100% 30%,
          100% 100%,
          40px 100%,
          0 50%,
          0 0
        );

        border-top: 8px solid #bfe2ff;
        border-bottom: 8px solid #bfe2ff;
        // min-height: 190px;
      }
    }
  }
}

article {
  padding: 2em 2em 0;
  display: grid;
  grid-template-columns: 60% auto;
  column-gap: 2em;
  grid-template-rows: auto 1fr;

  h1 {
    grid-area: 1 / 1 / 2 / 3;
    background: #fff;
    padding: 1em;
    text-align: center;
    border-top: 8px solid #bfe2ff;

    // margin-bottom: 2em;
  }

  section {
    position: relative;
    grid-area: 2 / 1 / 3 / 2;
    background: #fff;
    padding: 0 4em 1em;
    // display: grid;
    // align-content: center;
    // border-radius: 1em;
    // width: 60%;

    clip-path: polygon(
      calc(100% - 50px) 0,
      100% 30%,
      100% 100%,
      40px 100%,
      0 50%,
      0 0
    );

    letter-spacing: .5px;
    line-height: 1.4;
    border-bottom: 8px solid #bfe2ff;

    p:not(:first-child) {
      margin-top: 1em;
    }

    li {
      list-style: inside;
      padding-left: 2em;
    }

    button {
      position: absolute;
      bottom: 1em;
      left: 50%;
      transform: translateX(-50%);
      place-self: center;
      background: #337ab7;
      border: none;
      padding: 1rem 3em;
      font-size: 1em;
      color: #fff;
      border-radius: 20px;
      cursor: pointer;
    }
  }

  div {
    grid-area: 2 / 2 / 3 / 3;
    place-self: center;
    position: relative;
    transition: all 0.3s;
